import React from 'react';

class Header extends React.Component {

    render() {

        return (
        <div>
            <div className="video-header valign-wrapper">
                <div className="container p-40 valign-wrapper">
                    <div className="row mb0 p-40">
                        <div className="col s12">
                            <h1 className="white-text text-uppercase">BRINGING HUMANITY <br/>BACK TO SALES</h1>
                            <h5 className="white-text">Sales software has failed to keep up with the ever-changing selling landscape; <br className="hide-on-med-and-down"/>We are here to change that.</h5>
                            <br/>
                            <input type="text" className="black-text" /><input value="JOIN NOW" type="submit"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>



        );
    }
}

export default Header;


