import React, { Component } from 'react';
import axios from 'axios';
import VideoList from './VideoList';

const YOUTUBE_URL = 'https://www.googleapis.com/youtube/v3/playlistItems';
const API_KEY = "AIzaSyDXQ1H2iW0GC1wlN5X_U55Lhv2VX3QSjc4";

class VideosByPlaylist extends Component {

    constructor(props) {
        super(props);

        this.state = {
            selectedVideo: null,
            videos: null,
            showVideo: 'hide-me',
            autoplay: 0
        };

        this.loadVideos = this.loadVideos.bind(this);
        this.loadVideos();
    }

    componentDidMount() {

    }

    loadVideos() {

        // This is an example of the api we want to hit
        // https://www.googleapis.com/youtube/v3/playlistItems?part=snippet&playlistId=PLvYp_PztxWI7aprWkgQIHpaOQVpEVeAjv&key=AIzaSyDXQ1H2iW0GC1wlN5X_U55Lhv2VX3QSjc4

        const params = {
            part: 'snippet',
            key: API_KEY,
            playlistId: this.props.playlistId,
            type: 'video'
        };

        console.log(YOUTUBE_URL);
        axios.get(YOUTUBE_URL, { params: params })
            .then((response) => {
                console.log(response);
                this.setState({
                    selectedVideo: response.data.items[0],
                    videos: response.data.items
                });
            })
            .catch((error)=>{
                console.log(error);
            });


    }

    render() {

        if(this.state.videos === null){

            return (<div className="container">
                    <br/>
                    <br/>
                    <div className="progress brand-blue">
                        <div className="indeterminate light-blue darken-4"></div>
                    </div>
                    <br/>
                    <br/>
                </div>
            )

        }

        return (<div>

            <div className={this.state.showVideo}>

                <div className="video-takeover" onClick={() => {
                    this.setState({ showVideo: 'hide-me', autoplay: 0 });

                }}>
                    <div className="flex-center">
                    <div className="container">
                        <div className="video-container rounded">
                            <iframe width="853" height="480" src={`//www.youtube.com/embed/${this.state.selectedVideo.snippet.resourceId.videoId}?rel=0&autoplay=${this.state.autoplay}`} frameBorder="0" ></iframe>
                        </div>
                    </div>
                    </div>
                </div>

            </div>

            <VideoList
                onVideoSelect={selectedVideo => {
                    this.setState({ autoplay: 1, selectedVideo : selectedVideo, showVideo: 'show' });
                }}
                videos={this.state.videos}
            />
        </div>)
    }
}

export default VideosByPlaylist;


