import React from 'react';

const VideoList = (props) => {

    const videoItems = props.videos.map((video, onVideoSelect) => {

        return (
            <div className="col s12 m4" onClick={() => props.onVideoSelect(video)} >
                <img  src={video.snippet.thumbnails.maxres.url} alt="" className="responsive-img rounded skew-img clickable"/>
            </div>
        )
    });

    return (
        <div className="container">
            <div className="row">
                <div className="col s12">
                    <br/>
                    <br/>
                </div>
                {videoItems}
                <div className="col s12">
                    <br/>
                    <br/>
                </div>
            </div>
        </div>
    )
};

export default VideoList;



