import React from 'react';
import ReactDOM from 'react-dom';
import $ from "jquery";
import 'materialize-css/dist/js/materialize.js';
// import { Link } from 'react-router-dom';


class Nav extends React.Component {

    componentDidMount() {
        $(document).ready(function(){
            $(".button-collapse").sideNav();
        });
    }

    render() {

        return (
            <div>
                <nav className="transparent z-depth-0">
                    <div className="nav-wrapper container transparent">
                        <a href="/" className="brand-logo">
                            <img src="https://s3.amazonaws.com/aws-website-trueconnectlandingpage-yww97/img/logo-tc.png" alt="logo"/>
                        </a>
                        <a href="#" data-activates="mobile-demo" className="button-collapse right"><i className="fa fa-bars fa-2x brand-blue-text"/></a>
                        <ul id="nav-mobile" className="right hide-on-med-and-down text-uppercase bold-text">
                            <li className="right-line"><a href="#about">About Us</a></li>
                            <li className="right-line"><a href="#videos">Videos</a></li>
                            <li className="right-line"><a href="#contact">More Information</a></li>
                            <li><a href="#contact">Contact Us</a></li>
                        </ul>
                        <ul className="side-nav black" id="mobile-demo">
                            <li><a href="#about">About Us</a></li>
                            <li><a href="#videos">Videos</a></li>
                            <li><a href="#contact">More Information</a></li>
                            <li><a href="#contact">Contact Us</a></li>
                        </ul>
                    </div>
                </nav>
            </div>
        );
    }
}

export default Nav;



