import React from 'react';

class SectionTwo extends React.Component {

    render() {

        return (
            <div id="about">
                <section className="section section-two valign-wrapper">

                    <div className="flex-center">
                        <div className="container">

                            <h2 className="m0">ARE YOU TRULY <br className="hide-on-med-and-down"/>CONNECTING WITH YOUR <br className="hide-on-med-and-down"/>CLIENTS AND PROSPECTS?</h2>
                            <br className="hide-on-small-and-down"/>
                            <h5 className="m0">We all know that people buy from people hey like and trust. The challenge is building trust in a waythat's authentic and sustainable. TrueConnect allows our users to quickly build and maintain trust by ensuring our users Always Add Value to their client - Always!</h5>

                            <a className="waves-effect hide waves-light btn-large parallelogram" href="#">
                                <span className="skew-fix text-uppercase white-text">Coming Soon</span>
                            </a>
                        </div>
                    </div>
                </section>

                <div className="parallelogram-blue hide-on-small-and-down" />
            </div>
        );
    }
}

export default SectionTwo;


