import React from 'react';
import VideoCarousel from "./VideosByPlaylist";
import $ from "jquery";
import 'materialize-css/dist/js/materialize.js';

class SectionFive extends React.Component {

    componentDidMount() {

        $(document).ready(function(){
            $('ul.tabs').tabs();
            $('.dropdown-button').dropdown({
                    inDuration: 300,
                    outDuration: 225,
                    constrainWidth: false, // Does not change width of dropdown to that of the activator
                    hover: true, // Activate on hover
                    gutter: 0, // Spacing from edge
                    belowOrigin: false, // Displays dropdown below the button
                    alignment: 'left', // Displays dropdown with edge aligned to the left of button
                    stopPropagation: false // Stops event propagation
                }
            );
        });

    }

    render() {

        return (
            <div id="videos">
                <section className="section-five black">
                    <div className="container valign-wrapper">
                        <div className="row mb0 p-40">
                            <div className="col s12">
                                <h2 className="white-text text-uppercase bold-text">IF YOURE A CONNECTOR, <br className="hide-on-med-and-down"/>THESE VIDEOS ARE FOR YOU</h2>
                                <h5 className="white-text">Select a category and check out the videos below.</h5>
                            </div>
                        </div>
                    </div>
                    <div className="row p-40 hide-on-small-and-down">
                        <div className="col s12 m12">
                            <div className="container ">
                                <ul className="tabs transparent text-uppercase">
                                    <li className="tab col s3"><a className="active" href="#test1">Demonstrating Value</a></li>
                                    <li className="tab col s3"><a href="#test2">Prospecting</a></li>
                                    <li className="tab col s3"><a href="#test3">Clients for Life</a></li>
                                    <li className="tab col s3"><a href="#test4">Closing The Deal</a></li>
                                    {/*<li className="tab col s2"><a href="#test5">Clients for Life</a></li>*/}
                                </ul>
                                <div className="white-bar"></div>
                            </div>
                        </div>
                        <div id="test1" className="col s12">
                            <VideoCarousel playlistId="PLvYp_PztxWI6LgiLwM2ZJ4InoamQsLHhi"/>
                        </div>
                        <div id="test2" className="col s12">
                            <VideoCarousel playlistId="PLvYp_PztxWI4f02BJLpOBtbdq7qpl4RMm"/>
                        </div>
                        <div id="test3" className="col s12">
                            <VideoCarousel playlistId="PLvYp_PztxWI7m5SG-RpKSys0qHdMgfCob"/>
                        </div>
                        <div id="test4" className="col s12">
                            <VideoCarousel playlistId="PLvYp_PztxWI7aprWkgQIHpaOQVpEVeAjv"/>
                        </div>
                    </div>

                    <div>
                        <div className="row p-40 hide-on-med-and-up">
                            <div className="col s12 m12">
                                <div className="container blue-border-tb">
                                    <a className="dropdown-button text-uppercase white-text" href="/" data-activates="dropdown1">Choose Your Category<i className="right fa fa-plus brand-blue-text" aria-hidden="true"/></a>
                                    <ul id="dropdown1" className="dropdown-content">
                                        <li><a className="white-text" href="#!">Demonstrating Value</a></li>
                                        <li><a className="white-text" href="#!">Prospecting</a></li>
                                        <li><a className="white-text" href="#!">Handling Objection</a></li>
                                        <li><a className="white-text" href="#!">Clients for Life</a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>

        );
    }
}

export default SectionFive;




