import React, { Component } from 'react';

class SectionThree extends Component {

    constructor(props) {
        super(props);

        this.state = {
            showVideo: 'hide-me',
            autoplay: 0
        };

    }

    render() {


        return (

            <section className="section section-three black valign-wrapper">

                <div className={this.state.showVideo}>
                    <div className="video-takeover" onClick={() => {
                        this.setState({ showVideo: 'hide-me', autoplay: 0 });

                    }}>
                        <div className="flex-center">
                            <div className="container">
                                <div className="video-container rounded">
                                    <iframe width="853" height="480" src={`//www.youtube.com/embed/aqUiJoSCuyk?rel=0&autoplay=${this.state.autoplay}`} frameBorder="0" ></iframe>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


                <div onClick={() => {
                    this.setState({ showVideo: 'show', autoplay: 1 });

                }} >

                <img src="https://s3.amazonaws.com/aws-website-trueconnectlandingpage-yww97/img/video-slice.png" alt="ALWAYS ADD VALUE" className="responsive-img clickable"/>
                </div>
            </section>

        );
    }
}

export default SectionThree;


