import React from 'react';

class SectionFour extends React.Component {

    render() {

        return (

                <section id="OPPORTUNITY" className="section section-four black relative">

                    <div className="container p-40 absolute valign-wrapper">
                        <div className="row mb0 p-40">
                            <div className="col s12">
                                <br/>
                                <h2 className="white-text text-uppercase bold-text">NEVER MISS ANOTHER OPPORTUNITY TO CONNECT</h2>
                            </div>
                            <div className="col s12 l9">
                                <h5 className="white-text">TrueConnect is fundamentally changing the way salespeople connect with clients and prospects. Using artificial intelligence, machine learning, and natural language processing, Trueconnect recommends ways salespeople can Always Add Value, resulting in stronger client relationships, increased close rates and higher client retention. Sales software has failed to keep up with the ever-changing selling landscape; We are here to change that.</h5>
                            </div>
                        </div>
                    </div>
                    <div className="parallelogram-blue hide-on-small-and-down" />
                </section>

        );
    }
}

export default SectionFour;


